.ColorIdioms {
  background-image: linear-gradient(rgba(169, 169, 169, 0.6), rgba(230, 230, 230, 0.6)), url("https://cdn-images-1.medium.com/max/2000/1*5TRuG7tG0KrZJXKoFtHlSg.jpeg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
 }


.playground {
  max-width: 900px;
  height: 100%;
  margin: auto;
  background: rgba(248, 248, 255, 0.5);
  text-align: center; }
  .playground .question {
    font: small-caps bold 20px serif;
    padding: 20px;
  }
  .playground input[type=radio] {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1; }
  .playground .Label::before {
    content: none; }
  .playground .Label::after {
    content: '';
    width: 50px;
    height: 80%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 100px;
    border-radius: 50%;
    border: 3px solid #fff; }
  .playground .InputContainer {
    padding: 0;
    margin: 30px 0;
    overflow: hidden; }
  .playground .InputContainer:first-of-type > .Label::after {
    background: #1e90ff; }
  .playground .InputContainer:nth-of-type(2) > .Label::after {
    background: #00cc66; }
  .playground .InputContainer:nth-of-type(3) > .Label::after {
    background: red; }
  .playground .InputContainer:last-of-type > .Label::after {
    background: #000; }
  .playground label {
    display: flex;
    align-items: center;
    width: 90%;
    margin: 10px auto;
    min-height: 50px;
    text-align: left;
    background: linear-gradient(#4d4d4d 98%, #fff);
    color: #fff;
    cursor: pointer;
    transition: .5s ease-in-out;
    box-shadow: 0 8px 15px black;
    text-indent: 20px;
    font: 400 1rem Verdana,"sans-serif"; }

label.-correct {
  background: #00cc66; }

label.-wrong {
  background: #ff3300; }

.tick {
  margin-right: 10px;
  font-weight: 900;
  font-size: 20px;
  color: #00cc66; }

.congratulations {
  padding: 20px;
  text-align: left;
  font: 700 25px Courier,monospace;
  text-transform: uppercase;
  transition: ease-in-out; }

@media only screen and (max-width: 480px) {
  .wrapper {
    flex-direction: column;
    align-items: flex-start; }
  .playground {
    width: 100%; }
  label {
    padding: 8px 5px; }
  .playground .InputContainer {
    margin: 0; }
  }